.hero-slider {
    width: 100%;
    height:807px;
    position: relative;
    z-index: 0;
  }

  .slide{
    height: 800px;
  }

  .slide-shape {
      position: absolute;
      top: 50%;
      left: 0;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      z-index: -1;
  }
  
  @media (max-width: 991px) {
    .hero-slider {
      height: 700px;
    }
    .slide{
      height: 700px;
    }
    .slide-shape{
      display: none;
    }
  }
  
  @media (max-width: 767px) {
    .hero-slider {
      height: 600px;
    }

    .slide{
      height: 600px;
    }
  
    .slide-shape{
      display: none;
    }
    .hero-style-1 .slide-title h2{
      margin-bottom: 0;
    }
  }
  @media (max-width: 570px) {
    .theme-btn-s2{
      display: none;
    }
  }
  
  
  .hero-slider .swiper-slide {
    overflow: hidden;
    color: #fff;
  }
  
  .hero-slider .swiper-container {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
  
  .hero-slider .slide-inner {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: left;
  }
  

  .hero-slider .slick-dots {
    bottom: 35px;
    right: 20%;
    width: auto;
    top: 43%;
  }
  
  .hero-slider .slick-dots li {
    margin: 10px;
    display: block;
  }
  
  .slick-dots li.slick-active button:before {
      opacity:1;
      color: #e9bf3d;
      border:1px solid #e9bf3d;
