/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/


html {
  font-size: 18px;
}

body {
  font-family: 'Barlow Condensed', sans-serif!important;
  background-color: #fff;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

@media (max-width: 767px) {
  body {
    font-size: 18px;
  }
}

p {
  color: #787878;
  line-height: 1.63em;
  font-size: 18px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212122;
  line-height: 1.3em;
}

ul {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;

}

a:hover {
  text-decoration: none!important;
}

img {
  max-width: 100%;
}
p{
  font-family: 'Roboto', sans-serif
}
li{
  font-size: 18px;
}

.App{
  overflow: hidden;
  position: relative;
}
/*--------------------------------------------------------------
#0.2    Global Elements
--------------------------------------------------------------*/
.page-wrapper {
  position: relative;
  overflow: hidden;
}


.fi:before {
  margin: 0;
}

.section-padding {
  padding: 110px 0;
}

@media (max-width: 991px) {
  .section-padding {
    padding: 100px 0;
  }
}

@media (max-width: 767px) {
  .section-padding {
    padding: 60px 0;
  }
}


.theme-btn, .theme-btn-s2 {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
  padding: 12px 60px 12px 30px;
  border: 0;
  border-radius: 40px;
  position: relative;
  background: -webkit-linear-gradient(left,#18b760,#e9bf3d 50%,#18b760);
  background-size: 200%,1px;
  background-position: 0;
  transition: all .3s;
  -moz-transition: all .3s;
  -webkit-transition: all .3s;
}

.theme-btn:hover, .theme-btn-s2:hover {
  background-position: 100%!important;
  color: #fff;
}

.theme-btn i, .theme-btn-s2 i {
  position: absolute;
  right: 30px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.theme-btn-s2{
  background: -webkit-linear-gradient(left,#14b761,#3ac060 50%,#14b761);
  color: #fff;
  padding: 16px 30px 16px 30px;
}
.theme-btn:hover, .theme-btn-s2:hover, .theme-btn:focus, .theme-btn-s2:focus, .theme-btn:active, .theme-btn-s2:active {
  background-position: 100%!important;
  color: #fff;
}

@media (max-width: 767px) {
  .theme-btn, .theme-btn-s2 {
    padding: 12px 16px;
    font-size: 15px;
  }
  .theme-btn i, .theme-btn-s2 i {
    display: none;
  }
}



.theme-btn-s3 {
  background-color: #212122;
  color: #fff;
  display: inline-block;
  padding: 14px 30px;
  border: 0;
  border-radius: 0;
  position: relative;
}

.theme-btn-s3 i {
  position: absolute;
  right: 30px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.theme-btn-s3:hover, .theme-btn-s3:focus, .theme-btn-s3:active {
  background-color: #a90c0c;
  color: #fff;
}

@media (max-width: 767px) {
  .theme-btn-s3 {
    padding: 12px 16px;
    font-size: 13px;
  }
  .theme-btn-s3 i {
    display: none;
  }
}

.form input,
.form textarea,
.form select {
  border-color: #bfbfbf;
  border-radius: 0;
  outline: 0;
  box-shadow: none;
  color: #595959;
}

.form input:focus,
.form textarea:focus,
.form select:focus {
  border-color: #2db85d;
  -o-box-shadow: 0 0 5px 0 #ee1414;
  -ms-box-shadow: 0 0 5px 0 #ee1414;
